@font-face {
    font-family: "Manrope";
    src: local("Manrope"),
    url("fonts/Manrope.ttf") format("truetype");
    }


.App {
    font-family: "Manrope", sans-serif;
    font-weight: 800;
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    background-color: #202733;
    }

.container {
    font-size: 1.5rem;
    position: relative;
    box-sizing: border-box;
    width: 33.75rem;
    height: 20.75rem;
    padding: 3rem;
    text-align: center;
    color: #cee3e9;
    border-radius: 0.9375rem;
    background-color: #313a48;
    }

.title {
    font-size: 0.8125rem;
    margin-bottom: 1.5rem;
    letter-spacing: 0.4rem;
    text-transform: uppercase;
    color: #53ffaa;
    }

.advice {
    min-height: 7.125rem;
    letter-spacing: -0.01875rem;
    line-height: 2.375rem;
    margin-bottom: 2rem;
    }

button {
    position: absolute;
    bottom: -32px;
    left: 50%;
    width: 4rem;
    height: 4rem;
    cursor: pointer;
    transform: translateX(-50%);
    border: unset;
    border-radius: 50%;
    background-color: #53FFAA;
    background-image: url("./assets/icon-dice.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: initial;
    transition: all 0.5s ease-in-out;
    }

button:hover {
  box-shadow: 0 0 40px #53FFAA;
  }

@media only screen and (max-width: 1000px) {
    .container {
        width: 21.4375rem;
        height: 19.6875rem;
        font-size: 1.2rem;
        padding: 1.5rem;
        }
    .advice {
        line-height: 1.5rem;
        }
    }